import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Navbar.css'; //     Navbar
import HeartAnimation from './HeartAnimation'; //     

const Navbar = () => {
  const [showHearts, setShowHearts] = useState(false);

  const handleClick = () => {
    setShowHearts(true);
    setTimeout(() => {
      setShowHearts(false);
    }, 4200); //    2 
  };

  return (
    <nav className="navbar">
      <ul className="navbar-list">
        <li className="navbar-item"><Link to="/poems"></Link></li>
        
        <li className="navbar-item"><Link to="/memories"></Link></li>
        <li className="navbar-item"><Link to="/diary"></Link></li>
        <li className="navbar-item" onClick={handleClick}> </li>
      </ul>
      {showHearts && <HeartAnimation />}
    </nav>
  );
};

export default Navbar;
